<template>
  <div class="set">
    <foodHeaderNav title="信息" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="set-list">
      <div class="set-item" v-for="(item, index) in infoArr" :key="index">
        <span class="key">{{item.key}}</span>
        <span class="value">{{item.value}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import foodHeaderNav from "../common/foodHeaderNav";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    foodHeaderNav
  },
  computed: {
    ...mapState({
      orderInfo: state => state.food.orderInfo
    })
  },
  data() {
    return {
      infoArr: []
    };
  },
  mounted() {
    console.log("orderInfo:", this.orderInfo);

    //     accnt:"P11804280169"
    // amount:"430.00"
    // orderSta:"1"
    // pcDesc:"香樟园"
    // pluCounts:3
    // pluInfos:Array[3]
    // tableNo:"2
    this.infoArr = [
      {
        key: "餐厅 :",
        value: this.orderInfo.pcDesc
      },
      {
        key: "桌号 :",
        value: this.orderInfo.tableNo
      },
      {
        key: "人数 :",
        value: this.orderInfo.gsts
      },
      {
        key: "服务员 :",
        value: localStorage.waiter
      },
      {
        key: "就餐时间 :",
        value: this.orderInfo.eatTime ? this.orderInfo.eatTime : ""
      },
      {
        key: "支付方式 :",
        value: this.orderInfo.payName ? this.orderInfo.payName : "未支付"
      }
    ];
  },
  methods: {
    gotoBack() {
      this.$router.push("/foodOrder");
    }
  }
};
</script>
<style lang="less" scoped>
.set-list {
  padding-left: 0.4rem;
  background-color: #fff;
  .set-item:last-child {
    border: none;
  }
  .set-item {
    border-bottom: 1px solid#EEEEEE;
    padding-top: 0.32rem;
    padding-bottom: 0.32rem;
    padding-right: 0.453rem;
    text-align: left;
    .key {
      display: inline-block;
      width: 70px;
      text-align: right;
    }
    .value {
      text-align: left;
    }
  }
}
</style>




